@import '~assets/styles/h3Imports';

@mixin small-support-card {
  @media #{$small-down} {
    display: flex;
    height: 100%;

    .header,
    .edit-medication {
      align-items: center;
      display: inline;
      width: 100%;
    }

    h5 {
      margin-bottom: 0;
    }
  }
}

@mixin tablet-support-card {
  @media #{$tablet-only}, #{$medium-only}, (min-width: #{$mq-large-1}) and (max-width: #{$mq-large-post}) {
    display: flex;
    height: 100%;

    .header,
    .edit-medication {
      display: inline;
    }
  }
}

.wrapper {
  align-items: center;
  background-color: var(--white);
  border-radius: rem(8);
  box-shadow: var(--box-shadow);
  display: flex;
  flex-direction: column;
  height: 100%;
  margin: rem(16) 0;
  padding: var(--spacing-xs);
  width: 100%;

  &.success {
    background-color: var(--interface-success-background);
    border: 1px solid var(--interface-success);
  }

  .header,
  .edit-medication {
    align-items: center;
    display: flex;
    height: 100%;
    width: 100%;
  }

  .not-displayed {
    display: none;
  }

  [data-fontsize='AAA'] & {
    .header {
      align-items: center;
      display: inline;
      width: 100%;
    }

    [data-item='info'] {
      border-right: none;

      a,
      button {
        margin-right: 0;
      }
    }

    [data-element='MedicationQuestion'] {
      flex: 0 1 100%;
      margin-left: 0;
    }
  }

  @include small-support-card;
  @include tablet-support-card;
}
