@import 'assets/styles/h3Imports';
@import './mixins';

.medication-info {
  border-right: 1px solid rgba(#323264, 0.17);
  display: flex;
  flex: 0 1 54.54%;
  justify-content: space-between;

  button,
  a {
    font-size: rem(16);
    font-weight: 500;
    margin-right: var(--spacing-s);
    padding: 0;
    text-decoration: none;
    white-space: nowrap;
  }

  .heading {
    color: var(--gray-5);
    font-weight: 700;
    padding-right: rem(10);
    @include h5;
  }

  .info {
    align-items: center;
    display: inline-flex;

    @media #{$tablet-down} {
      [data-fontsize='AAA'] & {
        display: block;
      }
    }

    span {
      align-items: center;
      display: inline-flex;
    }
  }

  &.skipped-info {
    .info {
      span {
        --icon-background: var(--gray-1);
        --icon-stroke: var(--gray-2);
      }

      .heading {
        color: var(--gray-3);
      }
    }
  }

  @include tabletSupport;
  @include smallSupport;
}

.figure {
  margin-right: rem(16);
  max-width: rem(48);

  @media #{$tablet-down} {
    [data-fontsize='AAA'] & {
      margin-left: auto;
      margin-right: auto;
    }
  }
}
