@import 'assets/styles/h3Imports';

@mixin tablet-support-question {
  @media #{$tablet-only}, #{$medium-only}, (min-width: #{$mq-large-1}) and (max-width: #{$mq-large-post}) {
    height: 100%;
    margin-left: 0;
    margin-top: var(--spacing-xxs);

    &.success,
    &.skipped {
      .yes-selected,
      .no-selected {
        p {
          margin: 0;
        }
      }
    }
  }
}

@mixin small-support-question {
  @media #{$small-down} {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 0;
    width: 100%;

    .question {
      margin: var(--spacing-s) 0 var(--spacing-xs) 0;
    }

    &.success,
    &.skipped {
      display: inline-flex;
      flex-basis: auto;
      flex-direction: row;
      margin-top: rem(24);

      .question {
        align-items: initial;
        margin: 0;
        margin-right: auto;
      }

      .yes-selected,
      .no-selected {
        p {
          margin: 0;
        }
      }
    }

    .segmented-group {
      width: 100%;

      .segmented-group-medication {
        margin: auto;
        width: 100%;
      }
    }
  }
}

.medication-question {
  align-items: center;
  display: inline-flex;
  flex: 0 1 45.36%;
  justify-content: space-between;
  margin-left: rem(24);
  width: 100%;

  @media (min-width: 64.125em) {
    [data-fontsize='AA'] & {
      margin-left: rem(16);
    }
  }

  .yes-selected,
  .no-selected {
    align-items: center;
    display: inline-flex;

    svg {
      width: rem(24);
    }

    span,
    h5 {
      align-items: center;
      display: inline-flex;
    }

    h5 {
      font-size: rem(16);
      margin: 0;
    }
  }

  .yes-selected {
    --icon-stroke: var(--interface-success);

    svg {
      margin: 3px rem(16) 3px 0;
    }
  }

  .no-selected {
    --icon-stroke: var(--gray-3);

    svg {
      margin: rem(9) rem(16) rem(9) 0;
    }
  }

  .question {
    font-weight: 400;
  }

  .visible {
    display: none;
  }

  .segmented-group-medication {
    display: inline-flex;
    height: rem(40);
    width: rem(195);

    & * {
      align-items: center;
      border-width: 2px;
      display: inline-flex;
      font-size: rem(14);
      padding: rem(13) 0;
    }
  }

  @include small-support-question;
  @include tablet-support-question;
}

.bold {
  @include h5;

  color: var(--gray-5);
  font-size: rem(16);
  font-weight: 700;
}
