@import 'assets/styles/h3Imports';

@mixin tabletSupport {
  @media #{$tablet-only}, 
  #{$medium-only},
  (min-width: #{$mq-large-1}) and (max-width: #{$mq-large-post}) {
    border-bottom: 1px solid rgba(#323264, 0.17);
    border-right: none;
    padding-bottom: var(--spacing-xxs);

    button {
      margin-left: 0;
      margin-right: 0;
    }
  }
}

@mixin smallSupport {
  @media #{$small-down} {
    border-bottom: 1px solid rgba(#323264, 0.17);
    border-right: none;
    display: flex;
    flex-direction: column;

    button,
    a {
      margin: rem(16) 0;
    }

    .info {
      h5 {
        margin-right: 0;
        margin-top: 0;
      }
    }
  }
}
